#body {
    display: grid;
    row-gap: 20px;
    justify-items: center;
}

.bodySection {
    background-color: rgba(255, 255, 255, 0.82);
    border: 4px inset rgb(0, 0, 0, 0.85);
    border-radius: 25px;
    width: 90%;
    padding: 20px;
    transition: transform 0.5s
}

.bodySection:hover {
    transform: scale(1.02);
}

.buttonsContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.myButton {
    border-radius: 100px;
    cursor: pointer;
    display: inline-block;
    font-family: CerebriSans-Regular, -apple-system, system-ui, Roboto, sans-serif;
    padding: 7px 20px;
    text-align: center;
    text-decoration: none;
    transition: all 250ms;
    border: 0;
    color: white;
    font-size: 16px;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    margin: 15px;
}

.myButton:hover {
    transform: scale(1.12) rotate(-1deg);
}


#skillsGrid {
    display: grid;
    grid-template-columns: 1fr;
    column-gap: 20px;
    row-gap: 20px;
    width: 100%;
    justify-items: center;
    align-items: center;
}

#educationGrid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 20px;
    row-gap: 20px;
    width: 100%;
    justify-items: center;
    align-items: center;
}

.skillsGridItem {
    width: 85%;
    display: grid;
    justify-items: center;
    align-items: center;
    grid-template-columns: 1fr 1fr;
}

.skillIcon {
    width: 20px;
    justify-self: end;
    background-color: transparent;
}

li {
    display: grid;
    align-items: center;
    grid-template-columns: 1fr 1fr;
}

