#popUp {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(0, 0, 0, 0.6);
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
}

#popUpBoxCode {
  background-color: white;
  width: 90%;
  height: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#popUpBoxLink {
  background-color: rgb(255, 255, 255, 0.75);
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#popUpText {
  padding-left: 40px;
  overflow: auto;
  width: 100%;
  height: 100%;
}

.codep {
  display: block;
  margin-block-start: 0px;
  margin-block-end: 0px;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}

#copyLinkIcon {
  width: 30px;
  width: 30px;
  transition: 0.3s ease-in-out;
}

#copyLinkIcon:hover {
  transform: scale(1.22);
  cursor: pointer;
}

#popUpText::-webkit-scrollbar {
  display: block;
  width: 10px;
  position: absolute;
  right: 0px;
  height: 100%;
}

#popUpText::-webkit-scrollbar-track {
  background: #f1f1f1;
}

#popUpText::-webkit-scrollbar-thumb {
  background: #888;
}

#popUpText::-webkit-scrollbar-thumb:hover {
  background: #555;
}

video {
  width: 640px;
  height: 360px;
}

@media only screen and (max-width: 700px) {
  video {
    width: 100%;
  }

  #popUpBoxLink {
    width: 90%;
  }
}